"use client";

import React, { useEffect, useState } from "react";
import { AnimeCard } from "@/components/anime-card"; // âœ” fixed import

export default function TrendingPage() {
  const [animes, setAnimes] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  const fetchTrending = async () => {
    try {
      const res = await fetch("https://api.jikan.moe/v4/top/anime?filter=bypopularity");
      const data = await res.json();
      setAnimes(data.data || []);
    } catch (error) {
      console.error("Failed to fetch trending anime:", error);
    }
    setLoading(false);
  };

  useEffect(() => {
    fetchTrending();
  }, []);

  return (
    <main className="min-h-screen flex flex-col">
      <div className="px-4 pt-24 pb-12 max-w-7xl mx-auto w-full">
        
        {/* TITLE */}
        <h1 className="text-2xl sm:text-3xl font-bold mb-6 text-foreground">
          ðŸ”¥ Trending Anime
        </h1>

        {/* LOADING SKELETON */}
        {loading ? (
          <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-4">
            {Array.from({ length: 10 }).map((_, i) => (
              <div key={i} className="w-full aspect-[2/3] bg-muted animate-pulse rounded-xl" />
            ))}
          </div>
        ) : (
          <>
            {/* GRID ANIME */}
            <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-4">
              {animes.map((anime, index) => (
                <AnimeCard
                  key={anime.mal_id}
                  anime={anime}
                  rank={index + 1}
                />
              ))}
            </div>
          </>
        )}
      </div>

      {/* FOOTER */}
      <footer className="border-t border-border bg-card mt-auto">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8 sm:py-12">
          
          <div className="flex flex-col md:flex-row justify-between items-center gap-6">
            <div className="text-center md:text-left">
               <span className="font-bold text-xl tracking-tight">FeinimeList</span>
            </div>

            <nav className="flex flex-wrap justify-center gap-x-6 gap-y-3 text-sm text-muted-foreground">
               {['Home', 'Popular', 'Trending', 'About', 'Contact', 'FAQ'].map((item) => (
                 <a key={item} href="#" className="hover:text-primary transition-colors">
                   {item}
                 </a>
               ))}
            </nav>

            <div className="flex items-center gap-4">
              <a href="#" className="text-muted-foreground hover:text-[#1DA1F2] transition-colors">
                <svg className="w-5 h-5" viewBox="0 0 24 24" fill="currentColor">
                  <path d="M23.954 4.569c-.885..."></path>
                </svg>
              </a>
              <a href="#" className="text-muted-foreground hover:text-[#5865F2] transition-colors">
                <svg className="w-5 h-5" viewBox="0 0 71 55" fill="currentColor">
                  <path d="M60.1 4.55A59 59..."></path>
                </svg>
              </a>
            </div>
          </div>

          <div className="border-t border-border mt-8 pt-8 text-center text-muted-foreground text-sm">
            <p>FeinimeList Â© 2025. All rights reserved.</p>
          </div>

        </div>
      </footer>
    </main>
  );
}
